<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'GrowmodoTestimonials',

  props: {
    dark: {
      type: Boolean,
      default: false,
    },
    max: {
      type: Number,
      default: 5,
    },
  },

